.research-section {
  text-align: center;
  padding: 50px 0;
  background-color: #f9f9f9;
}

.research-section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.research-item {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.research-item h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
}

.research-item p {
  font-size: 1rem;
  color: #333;
  margin-bottom: 20px;
}

.research-item a {
  font-size: 1rem;
  color: #007bff;
  text-decoration: none;
}

.research-item a:hover {
  text-decoration: underline;
}
